<template>
 <div class="shopcart-container">
    <!--  -->
    <div class="address-container">
      <div class="mui-card">
        <!--  -->
        <router-link :to="{name: 'address'}" push>
          <h3>{{ address.user }} {{ address.phone }}</h3>
          <p>{{ address.area }} {{ address.det }}</p>
        </router-link>
      </div>
    </div>
    <!--  -->
    <div class="goods-list">
      <!--  -->
      <div class="mui-card">
        <div class="mui-card-content" v-for="(item) in goodsList" :key="item.id">
          <div class="mui-card-content-inner flex">
            <img :src="item.image" />
            <div class="info">
              <h1>{{ item.name }}</h1>
              <p class="flex">
                <span class="price">{{ item.price }}</span>
                <span>x{{ item.counts }}</span>
              </p>
            </div>
          </div>
        </div>
        <!--  -->
        <div class="process-info">
          <p>
            <strong></strong>
            <strong></strong>
          </p>
          <p>
            <span></span>
            <span></span>
          </p>
        </div>
        <!--  -->
        <div class="store-info">
          <strong></strong>
          <textarea v-model="note" type="text" placeholder=""></textarea>
        </div>
      </div>
    </div>
    <!--  -->
    <ul class="fare-info mui-card">
      <li class="fare-price flex">
        <span></span>
        <span class="red">{{getGoodsPrice }}</span>
      </li>
      <li class="fare-price flex">
        <span></span>
        <span class="red">0.00</span>
      </li>
      <li class="fare-price flex">
        <span>
          <strong></strong>
        </span>
        <span class="red">{{ getGoodsPrice }}</span>
      </li>
      <!--  -->
      <div class="flex">
        <button class="mui-btn mui-btn-primary mui-btn-block" @click="goOrderList"></button>
      </div>
    </ul>
  </div></template>
<script>
import { mapState, mapGetters } from "vuex";
export default {
  props:['id'],//id
  data() {
    return {
      goodsList: [],//
      note:"",
      address:this.$store.getters['user/getDefaultAddress']
    };
  },
  computed: {
    // 
    ...mapState("shopcar", ["buy"]),
    // true true 
    ...mapGetters("shopcar", ["getGoodsPrice", "getBuyCounts"]),
    
  },
  created() {
    this.getBuyGoods();
    this.selectAddress();
  },
  methods: {
    getBuyGoods() {
      // console.log(this.buy);
      // console.log(this.getGoodsPrice,this.getBuyCounts);
      // 
      this.buy.forEach(item => {
        this.$http.get("posts", { params: { id: item.id } }).then(sucess => {
          //  console.log(sucess);
          // 
          this.goodsList.push(sucess.data[0]);
          //  
          this.goodsList.forEach(item => {
            // console.log(item.id);
            // id
            item.counts = this.getBuyCounts[item.id];
          });
        }).catch(err=>{
            this.$toast('')
        });
      });
    },
    selectAddress(){
        //   
        // paramsid 
        if(this.id){
            // 
            this.$http.get('posts',{params:{id:this.id}}).then(sucess=>{
                this.address=sucess.data[0]
            }).catch(err=>{
                this.$toast('')
            })
        }
    },
    goOrderList(){
        this.$router.replace('/orderList')
    }
  }
};
</script>
<style lang="scss" scoped>
.flex {
  display: flex;
}
  

.shopcart-container {
  background: #eee;
  overflow: hidden;
  // 
  .goods-list {
    margin-top: 10px;
    .mui-card {
      margin: 0;
      .mui-card-content {
        border-bottom: 1px solid #eee;
        .mui-card-content-inner {
          align-items: center;
          padding: 10px;
          img {
            width: 100px;
          }
          .info {
            margin-left: 10px;
            box-sizing: border-box;
            width: 100%;
            overflow: hidden;
            h1 {
              font-size: 13px;
              font-weight: bold;
              line-height: 20px;
              padding-top: 22px;
            }
            p {
              flex-direction: row;
              align-items: center;
              justify-content: space-between;
              .price {
                font-size: 16px;
                font-weight: 700;
                color: red;
                flex: 1;
              }
            }
          }
        }
      }
      // 
      .process-info {
        padding: 10px;
        border-bottom: 1px solid #eee;
        p {
          display: flex;
          flex-direction: row;
          align-items: center;
          justify-content: space-between;
          color: #333;
          span {
            color: #999;
          }
        }
      }
      // 
      .store-info {
        padding: 10px;
        textarea {
          font-size: 13px;
        }
      }
    }
  }
  // 
  .fare-info {
    padding: 10px;
    margin: 10px 0 0 0;
    .fare-price {
      padding: 5px 0;
      display: flex;
      justify-content: space-between;
      .red {
        color: red;
        font-size: 16px;
      }
    }
  }
  // 
  .address-container {
    .mui-card {
         text-align: left;
      margin: 0;
      padding: 10px;
      h3 {
        color: #333;
        font-size: 16px;
        font-weight: bold;
      }
    }
  }
}
</style>

