<template>
  <div class="goods-info">
      <!--  -->
   <div class="mui-card">
      <div class="mui-card-content">
        <div class="mui-card-content-inner">
         <swiper :imgList="goodsinfo.swiperShow" />
        </div>
      </div>
    </div>
    <!-- -->
    <transition @@before-enter="beforeEenter" @enter="Enter" @after-enter="afterEnter">
    <div class="ball" v-show="ballFlag" ref="ball"></div>
    </transition>
    <!--  -->
    <div class="mui-card">
      <div class="mui-card-header">{{ goodsinfo.name }}</div>
      <div class="mui-card-content">
        <div class="mui-card-content-inner">
          <p class="price">
            
            <span>{{ goodsinfo.price }}</span>
          </p>
          <div v-if="goodsinfo.num">
            <p class="go-num">
               :<numBox :max="goodsinfo.num" countVal="1" :goodsId="goodsinfo.id" :goodsName="goodsinfo.name"/>
            </p>
            <p class="go-buy">
              <mt-button type="primary" size="small" @click="goOrderCreate"></mt-button>
              <mt-button type="danger" size="small" @click="addCar"></mt-button>
            </p>
          </div>
          <div v-else></div>
        </div>
      </div>
    </div>
    <!--  -->
    <div class="mui-card">
      <!--  -->
	<div class="mui-card-header">
        
    </div>
	<div class="mui-card-content">
         <div class="mui-card-content-inner">
        <p>:{{goodsinfo.hotspot}}</P>
        <p>:{{goodsinfo.num}}</P>
        <p>:{{goodsinfo.time}}</P>
        </div>
    </div>
    <!--  -->
    <div class="mui-card-header"></div>
    <div class="good-desc">
        <div class="mui-card-content">{{goodsinfo.detail}}</div>
    </div>
	
    </div>
  </div>
</template>
<script>
import swiper from '../../components/swiper'
import numBox from '../../components/numBox'
export default {
  props: ["pid"],// pid
  components:{
      swiper,
      numBox
  },
  data() {
    return {
      goodsinfo:{},//
      selectCount:1, //
      ballFlag:false  //
    };
  },
  created() {
    this.getGoodsInfo();
    //  numBox 
    this.$bus.$on('PassVal',(value)=>{
        // console.log(value);
        //
        this.selectCount=value.count
    })
  },
  methods: {
    getGoodsInfo() {
      //
      // console.log(this.pid)
      this.$http
        .get("posts", { params: { id: this.pid } })
        .then(res => {
          console.log(res);
          if (res.status >= 200 && res.status < 300) {
            this.goodsinfo = res.data[0];
          } else {
            this.$toast(res.statusText);
          }
        })
        .catch(err => {
          this.$toast("404");
        });
    },
    //
    addCar() {
      // vuex
      // console.log(this.$store.state);
      this.ballFlag=!this.ballFlag  //
      // store 
      this.$store.commit('shopcar/addShopCar',{
        id:this.pid,
        name:this.goodsinfo.name,
        count:this.selectCount,
        price:this.goodsinfo.price,
        max:this.goodsinfo.num,
        done:true,
        image:this.goodsinfo.image,
        time:this.goodsinfo.time
      })
      
    },
    // 
    // el
    // 
    beforeEenter(el){
      el.style.transform="translate(0,0)"
    },
    // 
    Enter(el,done){
      //  getBoundingClientRect()
    let ballPosition= this.$refs.ball.getBoundingClientRect()
      //  getBoundingClientRect()
      let badgePosition=document.getElementById('badge').getBoundingClientRect()
      let x=badgePosition.left-ballPosition.left
      let y=badgePosition.top-ballPosition.top
      el.style.transform=`translate(${x}px,${y}px)`
      el.style.transition = 'all .5s cubic-bezier(.4, -0.3, 1, .68)'
      //
      done()
    },
    // 
    afterEnter(){
      this.ballFlag=!this.ballFlag  //
    },
    goOrderCreate(){
          //
             // 
        if(JSON.parse(localStorage.getItem('address'))!==null && JSON.parse(localStorage.getItem('userID'))!==null){
          // true
            this.goodsinfo.done=true
            //
            this.$store.commit('shopcar/setBuy',{
                  id:this.pid,
                  name:this.goodsinfo.name,
                  count:this.selectCount,
                  price:this.goodsinfo.price,
                  max:this.goodsinfo.num,
                  done:true,
                  image:this.goodsinfo.image,
                  time:this.goodsinfo.time
      })
            this.$router.push({name:'orderCreate',params:{id:this.goodsinfo.id}})
        }else{
              if(JSON.parse(localStorage.getItem('userID'))==null){
                this.$toast('')
                this.$router.replace('/user')
              }
             else if(JSON.parse(localStorage.getItem('address'))==null){
                 this.$toast('')
                 this.$router.replace('/Address/add')
              }
        }
     

      
    }
  }
};
</script>
<style lang="scss" scoped>
.goods-info {
  background: #f1f1ff;
  overflow: hidden;
  .price {
    span {
      color: red;
      font-size: 14px;
      font-weight: bold;
    }
  }
  .go-buy {
    display: flex;
    margin: 10px 0 0px;
    justify-content: center;
    button {
      margin: 0 5px;
    }
  }
  .good-desc {
    background: #fff;
    padding: 5px;
    .mui-card-content{
        border-top: 1px solid gray;
        width: 95%;
        transform: translateX(10px);
    }
    h3 {
      font-size: 16px;
      color: #226aff;
      text-align: center;
      margin: 15px 0;
    }
    .content {
      font-size: 14px;
      line-height: 28px;
      img {
        width: 100%;
      }
    }
  }
  .go-num {
    div {
      margin-left: 10px;
    }
  }
  .ball {
    width: 15px;
    height: 15px;
    border-radius: 50%;
    position: absolute;
    background: red;
    z-index: 99;
    left: 132px;
    top: 390px;
    transform: translate(93px, 230px);
  }
}
.mui-card-header:after{
    height: 0;
}
 .ball {
    width: 15px;
    height: 15px;
    border-radius: 50%;
    position: absolute;
    background: red;
    z-index: 99;
    left: 150px;
    top: 390px;
    transform: translate(93px, 230px);
  }
</style>


