<template>
  <div class="category">
    <!--  -->
    <div class="menu-left">
      <ul class="menu-bar">
        <li
          v-for="(item, index) in menu_bar"
          :key="index"
          :class="{active:index==currentIndex}"
          @click="showActive(index)"
        >{{item.name}}</li>
      </ul>
    </div>
    <!--  -->
    <div class="menu-rigth" ref="imgList">
      <ul class="menu_item">
        <li v-for="menu_item in menu_bar" :key="menu_item.id" class="item">
          <h4>{{menu_item.name}}</h4>
          <ul class="menu_item_list">
            <li
              class="menu_item_list_imgs"
              v-for="(item_list, index) in menu_item.imagesList"
              :key="index"
            >
              <!-- id -->
              <routerLink
                :to="{name:'goodsList',params:{category_id:menu_item.id}}"
                class="image_url"
              >
                <div>
                  <img :src="item_list.image">
                </div>
                <span>{{item_list.phone_name}}</span>
              </routerLink>
            </li>
          </ul>
        </li>
      </ul>
    </div>
  </div>
</template>
<script>
// 
import BScroll from "better-scroll";
export default {
  data() {
    return {
      menu_bar: [], //
      currentIndex: 0, //    
      rightTops: [] //li 
    };
  },
  methods: {
    showActive(idx) {
      //
      this.currentIndex = idx; //
      let y = -this.rightTops[idx]; //
      this.rightScroll.scrollTo(0, y); //scrollTo x y
    },
    // BScroll
    // 
    initBScroll() {
      this.leftScroll = new BScroll(".menu-left", {
        click: true,
        mouseWheel: true
      });
      this.rightScroll = new BScroll(".menu-rigth", {
        click: true,
        // 
        mouseWheel: true,
        probeType: 3 //
      });
    },
    initBScrollTop() {
      // li
      const item = this.$refs.imgList.querySelectorAll(".item");
      const TopsArry = [];// li
      let top = 0;
      TopsArry.push(top); //0 
      for (let idx = 0; idx < item.length; idx++) {
        // offsetTop  clientHeight
        // console.log(item[idx].offsetTop);
        //  console.log(item[idx].clientHeight);
        //   top +=item[idx].offsetTop///top
        top += item[idx].clientHeight;// li
        TopsArry.push(top);//li
      }
      //   console.log(this.itemsTop);
      this.rightTops = TopsArry;//
      // rightScroll 
      this.rightScroll.on("scroll", pos => {
        let scrollY = Math.abs(pos.y); //Y ()y
        for (let i = 0; i < this.rightTops.length - 1; i++) {
          //   li 
          if (scrollY > TopsArry[i] && scrollY < TopsArry[i + 1]) {
            // currentIndex
            this.currentIndex = i;
          }
        }
      });
    }
  },
  mounted() {
    // 
    this.$indicator.open({ text: "..." });
       //   
    this.$http.get("posts", { params: { id: 66 } }).then(res => {
      this.$indicator.close();
      //    console.log(res.data[0].shop_item);
      this.menu_bar = res.data[0].shop_item;
    });
  },
  watch: {
    //menu_bar
    menu_bar() {
      //   $nextTick onload
      this.$nextTick(() => {
        this.initBScroll();
        this.initBScrollTop();
      });
    }
  }
};
</script>
<style lang="scss" scoped>
ul {
  list-style: none;
  padding: 0;
  margin: 0;
}
.active {
  background-color: red;
  color: white;
}
h4 {
  text-align: left;
}
.menu_item_list {
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;
  width: 100%;
  margin-top: 10px;
  margin-bottom: 10px;
  .menu_item_list_imgs {
    width: 33.3%;
    img {
      width: 100%;
    }
    span {
      color: gray;
    }
  }
}

.category {
  height: 100vh;
  display: flex;
  justify-content: flex-start;
}
.menu-left {
  height: 100%;
  .menu-bar {
    padding: 0;
    margin: 0;

    height: 100%;
    width: 100px;
    background-color: lavender;
    li {
      display: flex;
      flex-direction: column;
      justify-content: center;

      height: 50px;
      width: 100px;
    }
  }
}
</style>
