<template>
  <div class="login">
    <div class="mui-content">
      <form class="mui-input-group">
        <div class="mui-input-row">
          <label></label>
          <input
            v-model="reg.username"
            type="text"
            class="mui-input-clear mui-input"
            placeholder=""
          >
        </div>
        <div class="mui-input-row">
          <label></label>
          <input
            v-model="reg.password"
            type="password"
            class="mui-input-clear mui-input"
            placeholder=""
          >
        </div>
        <div class="mui-input-row">
          <label></label>
          <input
            v-model="reg.comfirm"
            type="password"
            class="mui-input-clear mui-input"
            placeholder=""
          >
        </div>
        <div class="mui-input-row">
          <label></label>
          <input
            v-model="reg.email"
            type="text"
            class="mui-input-clear mui-input"
            placeholder=""
          >
        </div>
      </form>
      <div class="mui-content-padded">
        <button @click="register" type="button" class="mui-btn mui-btn-block mui-btn-primary"></button>
        <div class="link-area">
          <p class="welcome"></p>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: "register",
  data() {
    return {
      // 
      reg: {
        username: "",
        password: "",
        comfirm: "",
        email: ""
      }
    };
  },
  methods: {
    register() {
      // 
      //
      if (this.reg.username.trim().length > 5) {
        this.$toast("5");
        return;
      } else if (this.reg.username.trim() === "") {
        this.$toast("");
        return;
      }
      if (this.reg.password.trim().length < 6) {
        this.$toast("6");
        return;
      } else if (this.reg.username.trim() === "") {
        this.$toast("");
        return;
      }
      if (this.reg.password !== this.reg.password) {
        this.$toast("");
        return;
      }
      if (
        !/[a-zA-Z0-9]+([-_.][A-Za-zd]+)*@([a-zA-Z0-9]+[-.])+[A-Za-zd]{2,5}$/.test(
          this.reg.email
        )
      ) {
        this.$toast("");
        return;
      } else if (this.reg.email.trim() === "") {
        this.$toast("");
        return;
      }
      // 
      this.$http
        .post("posts", this.reg)
        .then(res => {
          if (res.status === 200 || res.status < 300) {
            // localStorage
            // 
            this.$auth.setUserId(res.data.id);
            // 
            this.$auth.setUserName(res.data.username);
            // vuex
            this.$store.commit("user/setUser", res.data);
            // 
            this.$router.push("/user");
          } else {
            //
            this.$toast(res.statusText);
          }
        })
        .catch(err => {
          this.$toast("");
        });
    }
  }
};
</script>
<style lang="scss" scoped>
.welcome {
  text-align: center;
}
.mui-input-group {
  margin-top: 10px;
  background-color: transparent;
}
.mui-input-group label {
  width: 22%;
}
.mui-input-row:last-child {
  background: #fff;
}
.mui-input-row {
  margin-top: 20px;
  background: #fff;
}
.mui-input-row label ~ input,
.mui-input-row label ~ select,
.mui-input-row label ~ textarea {
  width: 78%;
}
.link-area {
  display: block;
  margin-top: 25px;
  text-align: center;
}
.mui-content-padded {
  margin-top: 30px;
}
</style>


