//    
function setUserAddress(name, data) {
    localStorage.setItem(name, JSON.stringify(data))
}
//    
function getUserAddress(name) {
    return JSON.parse(localStorage.getItem(name) || '[]')
}
export default {
    namespaced: true,
    state: {
        id: 0,
        username: '',
        userAddress: getUserAddress('address') //
    },
    actions: {},
    mutations: {
        setUser(state, user) {
            // 
            state.id = user.id
            state.username = user.username
        },
        clearUser(state, user) {
            state.id = user.id
            state.username = user.username
        },
        //   
        setAddress(state, address) {
            // 
            state.userAddress.push(address)
            //
            setUserAddress('address', state.userAddress)
        },
        //   
        EditAddress(state, edit) {
            // 
            state.userAddress = state.userAddress.filter(item => {
                // 
                if (!(parseInt(item.id) === parseInt(edit.id))) {
                    return item
                }
            })
            //
            state.userAddress.unshift(edit)
            // 
            setUserAddress('address', state.userAddress)
        },
        //   
        RemoveAddress(state, id) {
            state.userAddress = state.userAddress.filter(item => {
                // id
                if (!(item.id === id)) {
                    return item
                }
            })
            // 
            setUserAddress('address', state.userAddress)
        }
    },
    getters: {
        // 
        getAddress(state) {
            return state.userAddress
        },
        //  
        getDefaultAddress(state) {
            return state.userAddress[0]
        }

    }


}