<template>
  <div class="shopCar-container">
    <div class="mui-card">
      <div class="mui-card-content" v-for="item in car" :key="item.id">
        <div class="mui-input-row mui-checkbox mui-left">
          <label>&nbsp;</label>
          <input
            name="checkbox1"
            type="checkbox"
            :checked="item.done"
            @change="upDataDone(item.id)"
          >
        </div>
        <img src="../assets/images/phone.png">
        <div class="shopCar-text">
          <p class="name">{{item.name}}</p>
          <p class="price">{{item.price}}</p>
        </div>
        <div class="change-count">
          <numBox :max="item.num" :countVal="getGoodsCount[item.id]" :goodsId="item.id" size="min"/>
          <span @click="deleteGood(item.id)"></span>
        </div>
      </div>
    </div>
    <div class="mui-card">
      <div class="mui-card-content acount-card">
        <div class="acount-left">
          <p></p>
          <p>
            
            <span>{{getSelectCounts}}</span>
            <span>{{getGoodsPrice}}</span>
          </p>
        </div>
        <mt-button type="primary" class="acount-btn" @click="createOrder"></mt-button>
      </div>
    </div>
  </div>
</template>
<script>
// vuexstate
import { mapState, mapGetters } from "vuex";
import numBox from "../components/numBox.vue";
export default {
  components: {
    numBox
  },
  computed: {
    //  car
    ...mapState("shopcar", ["car"]),
    //   
    ...mapGetters("shopcar", [
      "getGoodsCount",
      "getSelectCounts",
      "getGoodsPrice"
    ]),
    // 
    ...mapGetters("user", ["getAddress"])
  },
  created() {
    this.upDataCar();
  },
  methods: {
    upDataCar() {
      //   console.log(this.car);
      // console.log(this.getGoodsCount);
      // 
      this.$bus.$on("PassVal", goodsinfo => {
        this.$store.commit("shopcar/updataGoodsInfo", goodsinfo);
      });
    },
    //  idid
    upDataDone(id) {
      // idstore
      this.$store.commit("shopcar/upDataDone", id);
    },
    //  idid
    deleteGood(id) {
      this.$store.commit("shopcar/deleteGoods", id);
    },
    createOrder() {
      // 
      // 
      if (this.getSelectCounts === 0) {
        this.$toast("");
      } else {
        //
        this.$store.commit("shopcar/setBuy");
        // 
        if (this.$auth.getUserId() === null) {
          this.$toast("");
          this.$router.push("/user");
        } else {
          // 
          if (this.getAddress.length === 0) {
            this.$toast("");
            this.$router.push("/Address/add");
          } else {
            // 
            this.$router.replace({ name: "orderCreate", params: { id: 666 } });
          }
        }
      }
    }
  }
};
</script>
<style lang="scss" scoped>
.mui-card-content {
  display: flex;
  justify-content: flex-start;
  img {
    width: 20%;
  }
  .mui-input-row {
    width: 10%;
    display: flex;
  }
  .mui-checkbox input[type="checkbox"] {
    top: 30px;

    left: 10px;
  }
  .mui-radio label,
  .mui-checkbox label {
    padding: 0px;
  }
  .shopCar-text {
    display: flex;
    flex-direction: column;
    justify-content: center;
    p {
      margin: 0px;
      font-size: 16px;
    }
    .name {
      color: black;
    }
    .price {
      color: red;
      text-align: left;
    }
  }
  .change-count {
    width: 40%;
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    color: red;
    font-size: 16px;
    white-space: nowrap;
    span {
      transform: translate(10px, -7px);
    }
  }
}
.acount-card {
  justify-content: space-between;
  .acount-left {
    text-align: left;
    margin-left: 2%;
    margin-top: 4%;
    span {
      color: red;
      font-size: 18px;
    }
  }
  .mint-button--primary {
    align-self: flex-end;
    margin-bottom: 2%;
    margin-right: 2%;
  }
}
</style>
