<template>
  <div class="goods-list">
    <div class="goods-item" v-for="(item,index) in goodsListArry" :key="index">
      <routerLink :to="{name:'GoodsInfo',params:{pid:item.pid}}">
        <img :src="item.image">
        <h1 class="title">{{ item.name }}</h1>
        <p class="info">
          <span class="price">{{ item.price }}</span>
          <span class="sell"> {{ item.number }} </span>
        </p>
      </routerLink>
    </div>
    <mt-button
      size="large"
      v-show="this.goodsListArry.length !==0"
      class="more"
      @click.once="getMore"
    ></mt-button>
  </div>
</template>
<script>
export default {
  // 
  props: ["category_id"],
  data() {
    return {
      goodsListArry: [],
      id: 0
    };
  },
  created() {
    this.getGoodsList(0);
  },
  methods: {
    getGoodsList(more) {
      // 
      this.$indicator.open({ text: "" });
      //id   idid+01
      this.id = this.category_id + "0" + "1";
      this.$http
        .get("posts", { params: { id: this.id } })
        .then(res => {
          this.$indicator.close();
          console.log(res);
          // console.log(res.data[0].spLists);
          if (more===0) {
            this.goodsListArry = res.data[0].spLists;
          } 
          if(more===1){
            // 
            this.goodsListArry = this.goodsListArry.concat(res.data[0].moreSp);
            this.$toast("");
          }
          // 
          if (res.data[0].spLists.length === 0) {
            this.$toast("" + this.id);
          }
        })
        .catch(err => {
          this.$toast("");
        });
    },
    getMore() {
      // 
      this.getGoodsList(1);
    }
  }
};
</script>
<style lang="scss" scoped>
.goods-list {
  display: flex;
  flex-wrap: wrap;
  padding-left: 10px;
  .goods-item {
    width: calc(calc(100% / 2) - 10px);
    margin: 10px 10px 0 0;
    background: #fff;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    border-radius: 10px;
    padding: 10px;
    img {
      width: 100%;
    }
    .title {
      font-size: 14px;
      color: #333;
      margin: 10px 0;
    }
    .info {
      display: flex;
      justify-content: space-between;
      margin-bottom: 0;
      .price {
        color: red;
        font-weight: bold;
        font-size: 16px;
      }
      .sell {
        font-size: 13px;
      }
    }
  }
  .more {
    margin: 15px 0;
    font-size: 14px;
  }
}
</style>


