<template>
  <div class="photoinfo-container">
    <!--  -->
    <h1 class="title">{{ newsinfolist.title }}</h1>
    <!--  -->
    <p class="photoinfo-nav">
      <span>{{ newsinfolist.add_time | dateFormat }}</span>
      <span>{{newsinfolist.click}}</span>
    </p>
    <hr>
      <!--  -->
    <div class="thumbs">
      <!-- vue-preview -->
        <!-- slides  @close-->
        <!-- wh  msrc-->
      <vue-preview :slides="lists"></vue-preview>
    </div>
    <!--  -->
    <div class="content">
      <p>
        <span>{{newsinfolist.content}}</span>
      </p>
    </div>
  </div>
</template>
<script>
export default {
  props: ["id"],//id params
  data() {
    return {
      newsinfolist: {}, //
      lists: [] //
    };
  },
  mounted() {
    // 
    this.$http.get("posts", { params: { id: this.id } }).then(sucess => {
      console.log(sucess);
      if (sucess.status >= 200 || sucess.status < 300) {
        this.newsinfolist = sucess.data[0].msg;
      }
    });
    this.handlerColse();
  },
  methods: {
    handlerColse() {
        this.lists=[]
      this.$http.get("posts", { params: { id: this.id } }).then(sucess => {
        console.log(sucess);
        if (sucess.status >= 200 || sucess.status < 300) {
          //  
          // console.log(sucess.data);
          sucess.data.forEach(item => {
            item.msg.img_url.w = 600;
            item.msg.img_url.h = 400;
            item.msg.img_url.msrc= item.msg.img_url.src //msrcsrc
            // 
            this.lists.push(item.msg.img_url);
          });
        }
      });
    }
  }
};
</script>
<style lang="scss" >
.photoinfo-container {
  padding: 0 5px;
  .title {
    font-size: 16px;
    text-align: center;
    margin: 10px 0;
    color: #26a2ff;
  }
  .photoinfo-nav {
    display: flex;
    justify-content: space-between;
    color: gray;
  }
  .content {
    text-align: left;
    span {
      font-size: 15px;
      color: black;
    }
  }
    .thumbs {
    .my-gallery {
      width: 100%;
      display: flex;
      flex-flow: row wrap;
    }
    figure {
      margin: 0.2em;
    }
    img {
      width: 5.6em;
      box-shadow: 0 0 8px #999;
    }
  }
}
</style>

