function setShop(name, item) {
    localStorage.setItem(name, JSON.stringify(item))
}
function getShop(name) {
    return JSON.parse(localStorage.getItem(name) || '[]')
}
let state = {
    //[ {id:'',num:0,done:false}]
    car: getShop('car'),//
    buy: getShop('buy') // 
}
let mutations = {
    addShopCar(state, goodsinfo) {
        //  
        let flag = false //   
        // 
        //  id
        state.car.some(item => {
            if (item.id === goodsinfo.id) {
                // 
                item.count += parseInt(goodsinfo.count)
                flag = true //
                return true
            }
        })
        //  
        if (!flag) {
            state.car.push(goodsinfo)
        }

        // 
        setShop('car', state.car)
    },
    updataGoodsInfo(state, goodsinfo) {
        //   
        state.car.some(itme => {
            // 
            if (itme.id === goodsinfo.id) {
                itme.count = parseInt(goodsinfo.count)
                return true
            }
        })
        // 
        setShop('car', state.car)
    },
    upDataDone(state,id) {
        //    
        state.car.some(item => {
            // id id
            if (item.id === id) {
                // 
                item.done = !item.done
                return true
            }
        })
        setShop('car', state.car)
    },
    deleteGoods(state, goodsID) {
        //goodsIDid goodsIDid
        //  
        state.car = state.car.filter(item => {
            // idid 
            return item.id !== goodsID
        })
        setShop('car', state.car)
    },
    setBuy(state,buyNow) {
        // buyNowture
        state.buy = []  // 
        if(buyNow){
            // 
            state.buy.push(buyNow)
        }
        state.car.forEach(item => {
            // true
            if (item.done) {
                state.buy.push(item)
            }
        })
        setShop('buy', state.buy)
    },
    cancelOrder(state, id) {
        //  idid 
        state.buy.forEach(item => {
            // false
                if (!item.done) {
                    // cancel
                    item.cancel = ""
                }
        })
        setShop('buy', state.buy)
    }
}
let getters = {
    // 
    getGoodsCount(state) {
        // id
        let counts = {} //{id}
        state.car.forEach(element => {
            counts[element.id] = element.count
        });
        return counts
    },
    //
    getSelectCounts(state) {
        let counts = 0
        state.car.forEach(item => {
            // 
            if (item.done) {
                counts += item.count
            }
        })
        return counts
    },
    //  true
    getGoodsPrice(state) {
        let price = 0
        state.car.forEach(item => {
            if (item.done) {
                // 
                price += parseInt(item.price * item.count)
            }
        })
        return price
    },
   
    getBuyCounts(state) {
        let counts = {} //
        // {id:}
        state.buy.forEach(item => {
            // id
            counts[item.id] = item.count
        })
        return counts
    },
    // 
    getBuy(state) {
        state.buy.forEach(item => {
            // 
            if (item.done) {
                item.cancel = ""
            }
        })
        setShop('buy', state.buy)
        return state.buy
    },

}
let actions = {
}
export default {
    namespaced: true,
    state,
    mutations,
    getters,
    actions,

}