<template>
    <div class="mui-numbox" data-numbox-min='1' :data-numbox-max='max'  :style="myStyle">
  <button class="mui-btn mui-numbox-btn-minus" type="button">-</button>
  <input 
  class="mui-numbox-input" 
  type="number" 
  :value="countVal" 
  @change="ChangeVal" 
  ref="ipt"
 />
  <button class="mui-btn mui-numbox-btn-plus" type="button">+</button>
</div>
</template>
<script>
import mui from '../lib/mui/js/mui.min.js'
export default {
    // max countVal goodsId 
    props:['max','countVal','goodsId','size'],
    data(){
        return {
            myStyle:{}
        }
    },
    mounted() {
        // numbox
        mui('.mui-numbox').numbox()
        // 
        if(this.size==="min"){
         this.myStyle={height:"25px","margin-bottom":"10px"}
        }
    },
    methods: {
       
    ChangeVal(){
        //
      const value=parseInt( this.$refs.ipt.value) //
       // 
      this.$bus.$emit('PassVal',{count:value,id:this.goodsId})
   
    }
},
//  numbox
watch:{
    max(newVal){
         //   
      mui('.mui-numbox').numbox().setOption('max',newVal)
    }
}
}

</script>

