                
<template>
  <!--    -->
  <form class="mui-input-group">
    <div class="mui-input-row">
      <label></label>
      <input type="text" class="mui-input-clear" placeholder="" v-model="form.user">
    </div>
    <div class="mui-input-row">
      <label></label>
      <input type="text" class="mui-input-clear" placeholder="" v-model="form.phone">
    </div>
    <div class="mui-input-row">
      <label></label>
      <input
        type="text"
        class="mui-input-clear"
        placeholder=""
        v-model="form.area"
        @click="choose"
      >
      <div class="divwrap">
        <!--  -->
        <div class="mask" v-show="show" @click="choose"></div>
        <!--province city area\\-->
        <v-distpicker
          type="mobile"
          v-show="show"
          :province="addressInfo.province"
          :city="addressInfo.city"
          :area="addressInfo.area"
          @province="onChangeProvince"
          @city="onChangeCity"
          @area="onChangeArea"
        />
      </div>
    </div>
    <div class="mui-input-row">
      <label></label>
      <input type="text" class="mui-input-clear" placeholder="" v-model="form.det">
    </div>
    <div class="mui-button-row">
      <button type="button" class="mui-btn mui-btn-primary mui-btn-block" @click="save"></button>
    </div>
    <div class="mui-button-row" v-show="id">
      <button type="button" class="mui-btn mui-btn-danger mui-btn-block remove" @click="remove"></button>
    </div>
  </form>
</template>
<script>
import VDistpicker from "v-distpicker"; //
export default {
  props: ["id"],//id
  components: {
    "v-distpicker": VDistpicker
  },
  data() {
    return {
      //
      form: {
        user: "",
        phone: "",
        area: "",
        det: ""
      },
      show: false, //
      // ,//
      addressInfo: {
        province: "",
        city: "",
        area: ""
      },
      address: []
    };
  },
  mounted() {
    // prarams id 
    if (this.id) {
      this.$http
        .get("posts", { params: { id: this.id } })
        .then(sucess => {
          //   console.log(sucess.data[0]);
          this.form = sucess.data[0];
        })
        .catch(err => {
          this.$toast("");
        });
    }
  },
  methods: {
    save() {
      // 
      if (this.form.user.trim().length > 5) {
        this.$toast("5");
        return;
      } else if (this.form.user.trim() === "") {
        this.$toast("");
        return;
      }
      if (
        !/^(13[0-9]|14[5-9]|15[0-35-9]|16[25-7]|17[0-8]|18[0-9]|19[0135689])[0-9]{8}$/.test(
          this.form.phone
        )
      ) {
        this.$toast("");
        return;
      } else if (this.form.phone.trim() === "") {
        this.$toast("");
        return;
      }
      if (this.form.area.trim() === "") {
        this.$toast("");
        return;
      }
      if (this.form.det.trim() === "") {
        this.$toast("");
        return;
      }
      //  
      if (!this.id) {
        // 
        this.$http
          .post("posts", this.form)
          .then(success => {
            // console.log(success.data);
            if (success.status >= 200 || success.status < 300) {
              this.$toast("");
              this.$router.replace({ name: "address" });
              // store
              this.$store.commit("user/setAddress", success.data);
            }
          })
          .catch(err => {
            this.$toast(err);
          });
      }
      //params 
      if (this.id) {
         //  store 
        this.$store.commit("user/EditAddress", this.form);
        // 
        this.$router.replace("/Address");
      }
    },
    remove(){
      // id
        this.$store.commit('user/RemoveAddress',this.id)
        this.$router.replace('/Address')
    },
    // v-distpicker
    // 
    //province city area\\
    // //
    onChangeProvince(data) {
      this.addressInfo.province = data.value;
    },
    onChangeCity(data) {
      this.addressInfo.city = data.value;
    },
    onChangeArea(data) {
      this.addressInfo.area = data.value;
      // //fromarea
      this.form.area = `${this.addressInfo.province}-${this.addressInfo.city}-${
        this.addressInfo.area
      }`;
      this.show = false;//
    },
    choose() {
      this.show = !this.show;//
    }
  }
};
</script>
<style lang="scss" scoped>
label {
  text-align: left;
}
.mui-input-group .mui-button-row {
  height: auto;
}
.mui-input-clear {
  font-size: 14px;
}
.mui-input-group:before {
  list-style: none;
}
.mui-button-row {
  margin-top: 20px;
  button {
    margin: 20px 0;
    padding: 10px 0;
  }
}
.tit {
  color: #999;
}
input::-webkit-input-placeholder {
  color: #999;
}
input::-moz-placeholder {
  /* Mozilla Firefox 19+ */
  color: #999;
}
input:-moz-placeholder {
  /* Mozilla Firefox 4 to 18 */
  color: #999;
}
input:-ms-input-placeholder {
  /* Internet Explorer 10-11 */
  color: #999;
}
.divwrap > .mask {
  background: #000;
  opacity: 0.3;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}
.divwrap > .distpicker-address-wrapper {
  color: #999;
  background: #fff;
  border-top: 1px solid #ccc;
  z-index: 1;
  height: 300px;
  overflow-y: auto;
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
}
.mui-button-row {
  margin-top: 0;
  padding-top: 0;
}
</style>

