//
// 
import VueRouter from 'vue-router'
// 

// 
import Home from '../pages/Home.vue'
import Category from '../pages/Category.vue'
import User from '../pages/User.vue'
import shopCar from '../pages/shopCar.vue'
import Login from '../pages/user/login.vue'
import Register from '../pages/user/register.vue'
import GoodsList from '../pages/goods/GoodsList.vue'
import GoodsInfo from '../pages/goods/GoodsInfo.vue'
import Address from '../pages/user/Address.vue'
import AddressEdit from '../pages/user/AddressEdit.vue'
import OrederCreate from '../pages/order/OrderCreate.vue'
import OrderList from '../pages/order/OrderList.vue'
import NewLists from '../pages/news/NewLists.vue'
import NewsInfo from '../pages/news/NewsInfo.vue'
import PhotoLists from '../pages/photo/PhotoLists.vue'
import PhotoInfo from '../pages/photo/PhotoInfo.vue'
const router= new VueRouter({
    // 
    routes:[
    //    
        {
            path:"/",
            redirect:"/home",//
        },
          // home
        {
             path:"/home",
             name:"home",
            component:Home,
            meta:{title:""}
        },
        // 
        {
             path:"/category",
             name:"category",
             meta:{title:""},
             component:Category
        },
        // 
        {
            path:"/shopCar",
            name:"shopCar",
            component:shopCar,
            meta:{title:""}
        },
        // 
        {
            path:"/user",
            name:"user",
            component:User,
            meta:{title:""},
        },
          // 
          {
            name:"login",
            path:"/user/login",
            component:Login,
            meta:{title:""}
        },
           // 
           {
            name:"register",
            path:"/user/register",
            component:Register,
            meta:{title:""}
        },
        // 
        {
            name:"goodsList",
            path:"/goodsList/:category_id",
            component:GoodsList,
            meta:{title:""},
            props:true //propsparams
        },
         // 
         {
            name:"GoodsInfo",
            path:"/GoodsInfo/:pid",
            component:GoodsInfo,
            meta:{title:""},
           props:true
        },
        // 
        {
            name:"address",
            path:"/Address",
            component:Address,
            meta:{title:""},
        },
        // 
        {
            name:"addressAdd",
            path:"/Address/add",
            component:AddressEdit,
            meta:{title:""},
        },
        // 
        {
            name:"addressEdit",
            path:"/Address/edit/:id",
            component:AddressEdit,
            meta:{title:""},
            props:true
        },
        // 
        {
            name:"orderCreate",
            path:"/order/create/:id",
            component:OrederCreate,
            meta:{title:""},
            props:true
        },
        // 
        {
            name:"orderList",
            path:"/orderList",
            component:OrderList,
            meta:{title:""},
        },
        // 
        {
            name:"newLists",
            path:"/newLists",
            component:NewLists,
            meta:{title:""},
        },
          // 
          {
            name:"news_show",
            path:"/newsInfo/:id",
            component:NewsInfo,
            meta:{title:""},
            props:true
        },
        // 
        {
            name:"photoLists",
            path:"/photo/list",
            component:PhotoLists,
            meta:{title:""},
        },
         // 
         {
            name:"photo_show",
            path:"/photo/show/:id",
            component:PhotoInfo,
            meta:{title:""},
            props:true
        },
    ]
})
//
 router.beforeEach((to,from,next)=>{
     if(to.meta.title){
        //  
         document.title=to.meta.title

     }
     next()
 })

export default router