<template>
    <div class="newsinfo-container">
    <!--  -->
    <h1 class="title">{{ newsinfolist.title }}</h1>
    <!--  -->
    <p class="newsinfo-nav">
      <span>{{ newsinfolist.add_time | dateFormat }}</span>
      <span>{{newsinfolist.click}}</span>
    </p>
    <hr>
    <!--  -->
    <div class="content" v-html="newsinfolist.content">
    </div>
  </div>
</template>
<script>
export default {
    props:['id'],
    data() {
        return {
            newsinfolist:{}
        }
    },
    mounted() {
        // 
        console.log(this.id);
        this.$http.get(`posts`,{params:{id:this.id}}).then(sucess=>{
            // console.log(sucess);
            this.newsinfolist=sucess.data[0]
        }).catch(err=>{
            this.$toast('')
        })
    },
}
</script>
<style lang="scss" scoped>
.newsinfo-container {
  padding: 0 5px;
  .title {
    font-size: 16px;
    text-align: center;
    margin: 10px 0;
    color: orangered;
  }
  .newsinfo-nav {
    display: flex;
    justify-content: space-between;
    color: #26a2ff;
  }

}
</style>


